/*

*/
<template>
    <div class="resource">
        <!--  -->
        <div style="height: 46px; line-height: 46px; overflow: hidden;">
            <el-row>
                <el-col :span="6">
                    <div style="padding-left:16px;">
                        <i class="el-icon-s-unfold"></i>
                        <span style="padding-left:9px;">
                            {{$store.state.AdminData.active_title}}
                        </span>
                    </div>
                </el-col>

                <el-col :span="18">
                    <div style="text-align: right; ">
                        <el-link @click="onSubMenu('onRefresh',true)" class="menu"></el-link>
                        <el-link @click="onSubMenu('onSearch',true)" class="menu"></el-link>

                        <el-link
                            class="menu" 
                            @click="onSubMenu(item)"
                            v-for="(item,index) in $store.state.AdminData.right_menus" 
                            :key="index">
                            {{item.name}}
                        </el-link>
                    </div>
                </el-col>
            </el-row>
        </div>
        <!--  -->
        <div style="border-top: solid 1px #f2f1f4;">
            <el-table 
                :data="rows"
                :height="height - 60 - 46 - 48"
                v-loading="loading"
                element-loading-text=""
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
                @sort-change="onSortChange"
                :highlight-current-row="true"
                @current-change="onSelectRow"
                style="width: 100%" 
                size="mini">
                
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="tag_name" label="" minWidth="300"></el-table-column>
                <el-table-column prop="from_type" label="" minWidth="500"></el-table-column>
                <el-table-column prop="add_time" label="" :formatter="common.formatDate" sortable align="center"></el-table-column>
                <el-table-column prop="add_time" label="" :formatter="common.formatDate" sortable align="center"></el-table-column>
            </el-table>
            
            <div class="page" :style="{width:width - 250 + 'px'}">
                <el-pagination
                    :current-page.sync="SearchFormData.page_num"
                    @current-change="onPageChange"
                    layout="prev, pager, next, jumper"
                    :total="total">
                </el-pagination>
            </div>
        </div>
        <!--  -->
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="search_show"
            width=400px>
            <el-form :model="SearchFormData" label-width="80px" size="small">
                <el-form-item label="">
                    <el-input v-model="SearchFormData.tag_name"/>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="search_show = false" size="small"> </el-button>
                <el-button type="primary" @click="onSearchSubmit" size="small"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="edit_show"
            width=400px>
            <el-form :model="EditFormData" label-width="80px" size="small">
                <el-form-item label="" prop="tag_name">
                    <el-input v-model="EditFormData.tag_name"/>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="edit_show = false" size="small"> </el-button>
                <el-button type="primary" @click="onEditSubmit" size="small"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="add_show"
            width=400px>
            <el-form :model="AddFormData" :rules="AddFormRule" label-width="80px" size="small">
                <el-form-item label="" prop="tag_name">
                    <el-input v-model="AddFormData.tag_name"/>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="add_show = false" size="small"> </el-button>
                <el-button type="primary" @click="onAddSubmit" size="small"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="detail_show"
            width=750px>
            <ul class="resource-tag-ul">
                <li>
                    <div></div>{{DetailFormData.from_type}}
                </li>
                <li>
                    <div></div>{{DetailFormData.tag_name}}
                </li>
                <li>
                    <div>:</div>{{DetailFormData.add_time}}
                </li>
                <li>
                    <div>:</div>{{DetailFormData.last_time}}
                </li>
            </ul>

            <span slot="footer">
                <!-- <el-button @click="DetailFormData = false" size="small"> </el-button> -->
                <el-button type="primary" @click="detail_show = false" size="small"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import { cookie_get } from "../../_commom/connectorHttp/trainingResourcesHttp.js";
import { ManageResourceTagList } from "../../_commom/connectorHttp/trainingResourcesHttp.js";
import { ManageResourceTagEdit } from "../../_commom/connectorHttp/trainingResourcesHttp.js";
import { ManageResourceTagAdd } from "../../_commom/connectorHttp/trainingResourcesHttp.js";
import { ManageResourceTagDel } from "../../_commom/connectorHttp/trainingResourcesHttp.js";
import { ManageResourceTagDetail } from "../../_commom/connectorHttp/trainingResourcesHttp.js";
import store from "@/store";
import util from "@/util.js";
export default {
    data () {
        return {
            // 
            rows:[],
            total:0,
            loading:false,
            curr_row:null,
            // 
            // 
            search_show: false,
            SearchFormData: {
                tag_name: '',
                start_date: '',
                end_date: '',
                page_no: 1,// 
                page_len: 10,//
                order_field:'add_time',//
                order_sort:'desc'//
            },
            // 
            edit_show: false,
            EditFormData: {
                tag_name: '',
            },
            // 
            add_show: false,
            AddFormData: {
                tag_name: ''
            },
            AddFormRule: {
                tag_name: [
                    { required: true, message: '', trigger: 'blur' },
                ]
            },
            // 
            detail_show: false,
            DetailFormData: {}
        }
    },
    created () {
        this.init();
    },
    computed:{
        width:() => {
            return store.state.AppData.width;
        },
        height:() => {
            return store.state.AppData.height;
        },
        upload_url:() => {
            return store.state.AppData.upload_url;
        },
        img_host:() => {
            return store.state.AppData.img_host;
        }
    },
    methods: {
        //  menu: local
        onSubMenu(menu, local = false) {
            util.submenu(menu,this,cookie_get (), local);
        },

        // 
        //  init
        init () {
            this.loading = true;
            var dataList = {
                tag_name: this.SearchFormData.tag_name,
                start_date: this.SearchFormData.start_date,
                end_date: this.SearchFormData.end_date,
                page_no: this.SearchFormData.page_no,// 
                page_len: this.SearchFormData.page_len,//
                order_field: this.SearchFormData.order_field,//
                order_sort: this.SearchFormData.order_sort,//
                login_token: cookie_get()
            }
            ManageResourceTagList (dataList).then(res => {
                if (res.code == 0) {
                    this.rows = res.data.rows;
                    this.total = res.data.total;
                } else {
                    this.$message.error(res.msg);
                }
                this.loading = false;
            })
            // 
            setTimeout(() => {
                this.loading = false;
            }, 10000);
        },
        // 
        onSortChange (column) {
            this.SearchFormData.order_field = column.prop;
            if (column.order == 'ascending') {
                this.SearchFormData.order_sort  = 'asc';
            } else {
                this.SearchFormData.order_sort  = 'desc';
            }
            this.init();
        },
        // 
        onSelectRow (row) {
            this.curr_row = row;
        },
        // 
        onPageChange (page) {
            this.SearchFormData.page_no = page;
            this.init();
        },

        // 
        // 
        onRefresh () {
            this.init();
        },
        // 
        onSearch () {
            this.search_show = true;
        },
        // 
        onSearchSubmit () {
            this.SearchFormData.page_num = 1;
            this.init();
            this.search_show = false;
            this.SearchFormData.tag_name = '';
        },
        // 
        handleEdit () {
            if (util.empty(this.curr_row)) {
                this.$message.error('');
                return;
            }
            this.EditFormData.tag_name = this.curr_row.tag_name;
            this.edit_show = true;
        },
        // 
        onEditSubmit () {
            var EditData = {
                tag_name: this.EditFormData.tag_name,
                uuid: this.curr_row.uuid,
                login_token: cookie_get()
            }
            ManageResourceTagEdit (EditData).then(res => {
                if (res.code == 0) {
                    this.init();
                } else {
                    this.$message.error(res.msg);
                }
                this.edit_show = false;
            })
        },
        // 
        handleAdd () {
            this.add_show = true;
        },
        // 
        onAddSubmit () {
            var AddData = {
                tag_name: this.AddFormData.tag_name,
                login_token: cookie_get()
            }
            ManageResourceTagAdd (AddData).then(res => {
                if (res.code == 0) {
                    this.$message.success('');
                    this.init();
                } else {
                    this.$message.error(res.msg);
                }
                this.add_show = false;
            })
        },
        // 
        handleDel () {
            if (util.empty(this.curr_row)) {
                this.$message.error('');
                return;
            }
            this.$confirm('','').then(() => {
                var DelData = {
                    uuid: this.curr_row.uuid,
                    login_token: cookie_get()
                }
                ManageResourceTagDel (DelData).then(res => {
                    if (res.code == 0) {
                        this.$message.success('');
                        this.init();
                    } else {
                        this.$message.error(res.msg);
                    }
                })
            })
        },
        // 
        handleDetail () {
            if (util.empty(this.curr_row)) {
                this.$message.error('');
                return;
            }
            var DetailData = {
                uuid: this.curr_row.uuid,
                login_token: cookie_get()
            }
            ManageResourceTagDetail (DetailData).then(res => {
                this.DetailFormData = res.data;
            })
        }
        
    }

}
</script>

<style lang="less">
    .resource{
        .menu{
            display: inline-block;
            padding:0 16px;
            text-align: center;
        }
        .page {
            height: 40px; 
            line-height: 40px; 
            text-align: right;
            position: fixed;
            bottom: 0;
            right:0;
            overflow: hidden;
        }
        .resource-tag-ul{
            li{
                line-height: 40px;
            }
        }
    }
</style>