<template>
    <!---->
    <div class="fromEditdrawer">
        <el-drawer
                :title="title"
                :visible.sync="is_show"
                direction="rtl"
                :size="width"
                :before-close="cancel">
            <div class="con">
                <hForm :form_des="form_des" @sure="sure" @cancel="cancel"></hForm>
            </div>

                <!--                <el-button @click="cancel"> </el-button>-->
                <!--                <el-button type="primary" @click="sure"> </el-button>-->

        </el-drawer>
    </div>


</template>

<script>
    import hForm from './hForm'

    export default {
        name: "fromEditdrawer",
        components:{hForm},
        data() {
            return {
                is_show: false,
            }

        },
        props: {
            title: {
                type: String,
                default: ''
            },
            width: {
                default: '50%'
            },
            form_des:{
                type:Array,
                default(){
                    return [];
                }
            }
        },
        methods: {
            /**/
            show() {
                this.is_show = true;
            },
            /**/
            hide() {
                this.is_show = false;
            },
            /**/
            cancel() {
                this.hide()

            },
            /**/
            sure(val) {

                this.hide();

                this.$emit('sure',val)

            }
        }
    }
</script>

<style scoped lang="less">

    .fromEditdrawer{

    }
    .con{

        padding: 0 40px;
    }
</style>