/**
 * @description 
 * */
 import  fieldDesConf from './fieldDesConf'
 import config from "../../config";

 import elementUtils from "./elementUtils";

export default {
    ...elementUtils,

     /**/
     getConfig(key){
         if(key){
             return config[key]||''
         }else {
             return  config;
         }


     },
    /*  fieldDesConf.js  table
    *
    * 
    * { //
        //     '0':'',
        //     '1':''
        // }
    * */
    getFieldDesConf(model,key){
        let data = fieldDesConf[model]||[];
        data=data[key];

        let re = {};//
        for (let i in data){
            let item  =data[i];
            re[item.value+''] = item;

        }
        return re;




    },
    /*  fieldDesConf.js  from*/
    getFromFieldDesConf(){

    },
    /**/
    checkParams(val='_213123',type='pwd'){

        let re = {
            is_check:false,//
            msg:'',//

        };
        let r='';

        switch(type) {
            case 'tel'://
                if (typeof val !== 'string'){
                    re.msg='';
                }
                if(val.length!==11){
                    re.msg='';
                }else {


                    if(val[0] ==='1'){
                        re.is_check=true;
                        re.msg='';
                    }
                }



                break;

            case 'pwd':
                    r = /^(?![0-9]+$)(?![a-z]+$)(?![A-Z]+$)(?!([^(0-9a-zA-Z)])+$).{6,20}$/.test(val)
                    if(r){
                        re.is_check=true;
                        re.msg='';
                    }else {
                        re.is_check=false;
                        re.msg='6-20,,,';
                    }


                break;

        }


        return re;




    }


    /**
     * @description 
     * @return boolean
     * */
    ,objInner(big_obj,inner_obj){


        for (let i in inner_obj){

            if( inner_obj[i] !== big_obj[i]){

                return  false;
            }

        }
        return true;


    }


    /* */
    ,editObjField(arr_obj,obj,new_obj){


        for (let i =0 , len = arr_obj.length; i < len;i++){


            let item = arr_obj[i];

            // 

           let is_inner =   this.objInner(arr_obj[i],obj);

           if(is_inner){

               Object.assign(item,new_obj)

           }


        }
        // console.log(arr_obj)



    },
    /**/
    val2Number(val){
        let num = '';
        num = parseInt(val)

        if (isNaN(num)){
            num=''
        }
        return num
    },

}