                                <el-button size="small" type="primary" :disabled="AddFormData.cont_type == 2 ? false : true"></el-button>
                                <!-- <div slot="tip" class="el-upload__tip">jpg/png500kb</div> -->
                            </el-upload>
                        </div>
                    </el-form-item>
                    <el-form-item label="">
                        <el-upload
                            class="upload-demo"
                            action="string"
                            :http-request="UploadImage"
                            list-type="picture">
                            <el-button size="small" type="primary"></el-button>
                            <!-- <div slot="tip" class="el-upload__tip">jpg/png500kb</div> -->
