                    </el-form-item>
                    <!-- <el-form-item label="" prop="encrypt">
                        <el-switch v-model="AddFormData.encrypt">
                        </el-switch>
                    </el-form-item> -->
                    <el-form-item label="" prop="is_show">
                        <el-switch v-model="AddFormData.is_show">
                        </el-switch>
