                </li>
                <li>
                    <p><span></span><span>{{create_detail.title}}</span></p>
                </li>
                <li>
                    <p><span></span><span>{{create_detail.content_time}}</span></p>
                </li>
                <!-- <li>
                    <p><span>icon</span><span>{{create_detail.icon}}</span></p>
                </li> -->
                <li>
                    <p><span></span><span>{{create_detail.look_num}}</span></p>
                </li>
                <li>
                    <p><span></span><span>{{create_detail.state == 0 ? '' : (create_detail.state == -1 ? '' : (create_detail.state == 1 ? '' : (create_detail.state == 2 ? '' : '')))}}</span></p>
                </li>
                <li>
                    <p><span></span><img :src="create_detail.thumb" style="width: 200px;"></p>
                </li>
                <li>
                    <p><span></span><span>{{create_detail.is_pay == 0 ? '' : ''}}</span></p>
                </li>
                <li>
                    <p><span></span><span>{{create_detail.remark}}</span></p>
                </li>
                <li>
                    <p><span></span><span>{{create_detail.min_num}}</span></p>
                </li>
                <li>
                    <p><span></span><span>{{create_detail.add_time}}</span></p>
                </li>
                <li>
                    <p><span></span><span>{{create_detail.encrypt == 0 ? '' : ''}}</span></p>
                </li>
                <li v-if="create_detail.video_type == 1 || create_detail.video_type == 2 || create_detail.video_type == 3">
                    <p><span></span><span>{{create_detail.video_type == 1 ? videoType1 : (create_detail.video_type == 2 ? videoType2 : videoType3)}}</span></p>
                </li>
                <li>
                    <p>
                        <span></span>
                        <span style="display: line-block;">
                            <div v-if="videoPlayer1" class="prism-player" id="J_prismPlayer" style="width: 300px;height: 300px;border: 1px solid red;"></div>
                            <video v-if="videoPlayer2" width="320" height="240" :src="videoUrl" controls>
                                <!-- <source :src="videoUrl" type="video/mp4">
                                <source :src="videoUrl" type="video/ogg">
                                Video -->
                            </video>
