/*
    
*/
<template>
    <div class="template">
        <!--  -->
        <div style="height: 46px; line-height: 46px; overflow: hidden;">
            <el-row>
                <el-col :span="6">
                    <div style="padding-left:16px;">
                        <i class="el-icon-s-unfold"></i>
                        <span style="padding-left:9px;">
                            {{$store.state.AdminData.active_title}}
                        </span>
                    </div>
                </el-col>

                <el-col :span="18">
                    <div style="text-align: right; ">
                        <el-link @click="onSubMenu('onRefresh',true)" class="menu"></el-link>
                        <el-link @click="onSubMenu('onSearch',true)" class="menu"></el-link>

                        <el-link
                            class="menu" 
                            @click="onSubMenu(item)"
                            v-for="(item,index) in $store.state.AdminData.right_menus" 
                            :key="index">
                            {{item.name}}
                        </el-link>
                    </div>
                </el-col>
            </el-row>
        </div>
        <!--  -->
        <div style="border-top: solid 1px #f2f1f4;">
            <el-table 
                :data="rows"
                :height="height - 60 - 46 - 48"
                v-loading="loading"
                element-loading-text=""
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
                @sort-change="onSortChange"
                :highlight-current-row="true"
                @current-change="onSelectRow"
                style="width: 100%" 
                size="mini">
                
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="real_name" label=""></el-table-column>
                <el-table-column prop="mobile" label=""></el-table-column>
                <el-table-column prop="role_name" label="">
                    <template slot-scope="scope">
                        {{scope.row.role_name == '' ? '' : scope.row.role_name}}
                    </template>
                </el-table-column>
                <el-table-column prop="add_time" label="" :sortable=true></el-table-column>
                <el-table-column 
                    prop="last_time" 
                    label="" 
                    align="center"
                    :sortable=true>
                    <template slot-scope="scope">
                        {{scope.row.last_time == 0 ? '' : scope.row.last_time}}
                    </template>
                </el-table-column>
                <el-table-column prop="last_ip" label="IP" align="center">
                    <template slot-scope="scope">
                        {{scope.row.last_ip == 0 ? '-' : scope.row.last_ip}}
                    </template>
                </el-table-column>
            </el-table>
            
            <div class="page" :style="{width:width - 250 + 'px'}">
                <el-pagination
                    :current-page.sync="SearchFormData.page_num"
                    @current-change="onPageChange"
                    layout="prev, pager, next, jumper"
                    :total="total">
                </el-pagination>
            </div>
        </div>
        <!--  -->
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="search_show"
            width=400px>
            <el-form :model="SearchFormData" label-width="80px" size="small">
                <el-form-item label="">
                    <el-input v-model="SearchFormData"/>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="search_show = false" size="small"> </el-button>
                <el-button type="primary" @click="onSearchSubmit" size="small"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="edit_show"
            width=400px>
            <el-form :model="EditFormData" label-width="80px" size="small">
                <el-form-item label="">
                    <el-input v-model="EditFormData"/>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="EditFormData = false" size="small"> </el-button>
                <el-button type="primary" @click="onEditSubmit" size="small"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="add_show"
            width=400px>
            <el-form :model="AddFormData" rules="AddFormRule" label-width="80px" size="small">
                <el-form-item label="" prop="">
                    <el-input v-model="AddFormData"/>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="AddFormData = false" size="small"> </el-button>
                <el-button type="primary" @click="onAddSubmit" size="small"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="detail_show"
            width=750px>
            <ul class="plann-template-ul">
                <li>
                    <p><span></span><span>{{}}</span></p>
                    <p><span></span><span>{{}}</span></p>
                </li>
                <li>
                    <p><span></span><span>{{}}</span></p>
                    <p><span></span><span>{{}}</span></p>
                </li>
                <li>
                    <p><span></span><span>{{}}</span></p>
                    <p><span></span><span>{{}}</span></p>
                </li>
                <li>
                    <p><span></span><span>{{}}</span></p>
                    <p><span></span><span>{{}}</span></p>
                </li>
            </ul>

            <span slot="footer">
                <!-- <el-button @click="DetailFormData = false" size="small"> </el-button> -->
                <el-button type="primary" @click="DetailFormData = false" size="small"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import store from '@/store';
    export default {
        data () {
            return {
                // 
                rows:[],
                total:0,
                loading:false,
                curr_row:null,
                // 
                // 
                search_show: false,
                SearchFormData: {
                    page_num: 1,// 
                    page_len: 10,//
                    order_field:'add_time',//
                    order_sort:'desc'//
                },
                // 
                edit_show: false,
                EditFormData: {

                },
                // 
                add_show: false,
                AddFormData: {

                },
                AddFormRule: {

                },
                // 
                detail_show: false
            }
        },
        created () {

        },
        computed:{
            width:() => {
                return store.state.AppData.width;
            },
            height:() => {
                return store.state.AppData.height;
            },
            upload_url:() => {
                return store.state.AppData.upload_url;
            },
            img_host:() => {
                return store.state.AppData.img_host;
            }
        },
        methods: {
            // 
            //  init
            init () {

            },
            // 
            onSortChange (column) {
                this.SearchFormData.order_field = column.prop;
                if (column.order == 'ascending') {
                    this.SearchFormData.order_sort  = 'asc';
                } else {
                    this.SearchFormData.order_sort  = 'desc';
                }
                this.init();
            },
            // 
            onSelectRow (row) {
                this.curr_row = row;
            },
            // 
            onPageChange (page) {
                this.SearchFormData.page_num = page;
            },

            // 
            // 
            onRefresh () {
                this.init();
            },
            // 
            onSearch () {
                this.search_show = true;
            },
            // 
            onSearchSubmit () {
                
            },
            // 
            // 
            onEditSubmit () {
                
            },
            // 
            // 
            onAddSubmit () {
                
            },
            // 
            handleDel () {
                if (util.empty(this.curr_row)) {
                    this.$message.error('');
                    return;
                }
                this.$confirm('','').then(() => {
                    // 
                })
            }
        }
    }
</script>

<style lang="less">
    .template{
        .menu{
            display: inline-block;
            padding:0 16px;
            text-align: center;
        }
        .page {
            height: 40px; 
            line-height: 40px; 
            text-align: right;
            position: fixed;
            bottom: 0;
            right:0;
            overflow: hidden;
        }
        .plann-template-ul{
            list-style: none;
            li{
                line-height: 40px;
                overflow: hidden;
                p:nth-of-type(1){
                    float: left;
                }
                p:nth-of-type(2){
                    float: right;
                }
                p{
                    span:nth-of-type(1){
                        width: 150px;
                        text-align: right;
                        display: inline-block;
                    }
                    span:nth-of-type(2){
                        width: 250px;
                        text-align: right;
                        display: inline-block;
                    }
                }
            }
        }
    }
</style>