<template>
    <!--

    http://www.wangeditor.com/index.html 
    -->
    <div id="editor"></div>


</template>

<script>
    import E from 'wangeditor'
    import lime from "@/lime";

    import FileHttp from "../../_commom/http/FileHttp";
    export default {
        name: "wangeditor",
        components:{ },
        data() {
            return {}

        },
        props:{
          value:{
              default:''
          }
        },
        methods: {

            /**/
            upload( file){

                let formData = new FormData(); //form
                let timestamp = lime.get_timestamp();
                let up_type = 'file';
                let login_token = lime.cookie_get('login_token')

                formData.append('file', file);//appendform
                formData.append('up_type', 'file');//appendform
                formData.append('timestamp', timestamp);//appendform
                formData.append('login_token', login_token);//appendform
                formData.append('sign', lime.md5_str(lime.set_sign({timestamp, up_type, login_token})));//appendform


                return new Promise((resolve, reject) => {
                    FileHttp.uploadFile('OssManageUpload', formData)
                        .then(res => {

                            let url = res.data.url;

                            resolve(url)

                        })

                })

            },
            createEditor(ele){
                const editor = new E(ele)

//  500px
                editor.config.height = 200
                editor.config.uploadImgMaxLength = 1 //  5 
                editor.config.customUploadImg =   (resultFiles, insertImgFn)=> {
                    // resultFiles  input 
                    // insertImgFn  url 

                    // 

                    this.upload(resultFiles[0])
                    .then(url=>{
                        insertImgFn(url)
                    })

                    // insertImgFn(imgUrl)/
                }
                //  onchange 
                editor.config.onchange =   (newHtml)=> {
                   this.$emit('editorChange',newHtml )
                }
//  onchange  200ms
                editor.config.onchangeTimeout = 500 //  500ms

                editor.config.menus = [
                    // 
                    'head', // 
                    'bold', // 
                    'fontSize', // 
                    'fontName', // 
                    'italic', // 
                    'underline', // 
                    'strikeThrough', // 
                    'foreColor', // 
                    'backColor', // 
                    'link', // 
                    'list', // 
                    'justify', // 
                    'quote', // 
                    'emoticon', // 
                    'image', // 
                    'table', // 
                    'code', // 
                    'undo', // 
                    'redo' // 
                ];
                editor.create();
                editor.txt.append(this.value)
            }
        },
        mounted() {
            this.createEditor('#editor')

        }
    }
</script>

