import Http from '../http/Http'

export default {
    /**
     * @description 
     * @return Promise
     * */
    ManageMemberTradeList(params) {
        return new Promise((resolve, reject) => {
            Http.httpPost('ManageMemberTradeList', params)
                .then(res => {
                    resolve(res)

                })
                .catch(err => {
                    reject(err)
                })
        })

    },
    /**
     * @description 
     * @return Promise
     * */
    ManageShopTradeList(params) {
        return new Promise((resolve, reject) => {
            Http.httpPost('ManageShopTradeList', params)
                .then(res => {
                    resolve(res)

                })
                .catch(err => {
                    reject(err)
                })
        })

    },

}