export default {
    /*base64*/
    img2base64(file){
        return new Promise((resolve, reject) => {
            var reader = new FileReader(); //
            reader.readAsDataURL(file); // DataURL,base64
            reader.onload = function(ev) { //

                var dataURL = ev.target.result; //DataURL,base64

                resolve(dataURL)
            }
        })

    },
}