        UploadImage(param){
            let file = param.file;
            let formData = new FormData(); //form
            let timestamp = lime.get_timestamp();
            let up_type = 'file';
            let  login_token= lime.cookie_get('login_token')

            formData.append('file', file);//appendform
            formData.append('up_type', 'file');//appendform
            formData.append('timestamp', timestamp);//appendform
            formData.append('login_token', login_token);//appendform
            formData.append('sign', lime.md5_str(lime.set_sign({timestamp, up_type, login_token})));//

            OssManageUpload(
                formData ,
                {'Content-Type':'multipart/form-data'}
            ).then(response => {
                this.AddFormData.pic_urls.push(response.data.url);
                console.log(this.AddFormData)
            }).catch(response => {
            })

        },
        // 
        //  init
        init () {
            this.loading = true;
            var dataList = {
                title: this.SearchFormData.title,
                is_pay: this.SearchFormData.is_pay,
                is_show: this.SearchFormData.is_show,
                ques_type: this.SearchFormData.ques_type,
                add_time: this.SearchFormData.add_time,
                page_no: this.SearchFormData.page_no,// 
                page_len: this.SearchFormData.page_len,//
                order_field: this.SearchFormData.order_field,//
                order_sort: this.SearchFormData.order_sort,//
                login_token: cookie_get()
            }
            ManageQuesQuestionList (dataList).then(res => {
                if (res.code == 0) {
                    this.rows = res.data.rows;
                    console.log(this.rows)
                    for (var i = 0;i<res.data.rows.length;i++) {
                        if (res.data.rows[i].is_pay == 0) {
                            this.rows[i].is_pay = '';
                        } else {
                            this.rows[i].is_pay = '';
                        }
                        if (res.data.rows[i].is_show == 0) {
                            this.rows[i].is_show = '';
                        } else {
                            this.rows[i].is_show = '';
                        }
                        if (res.data.rows[i].check_state == 0) {
                            this.rows[i].check_state = '';
                        } else if (res.data.rows[i].check_state == 1) {
                            this.rows[i].check_state = '';
                        } else if (res.data.rows[i].check_state == 2) {
                            this.rows[i].check_state = '';
                        } else if (res.data.rows[i].check_state == 3) {
                            this.rows[i].check_state = '';
                        }
                        if (res.data.rows[i].ques_type == 1) {
                            this.rows[i].ques_type = '';
                        } else if (res.data.rows[i].ques_type == 2) {
                            this.rows[i].ques_type = '';
                        } else if (res.data.rows[i].ques_type == 3) {
                            this.rows[i].ques_type = '';
                        } else if (res.data.rows[i].ques_type == 4) {
                            this.rows[i].ques_type = '';
                        } 
                    }
                    this.total = res.data.total;
                } else {
                    this.$message.error(res.msg);
                }
                this.loading = false;
            })
            // 
            setTimeout(() => {
                this.loading = false;
            }, 10000);
