import Vue from 'vue'
import VueRouter from 'vue-router'
import lime from "@/lime.js";

import operatorRouter from '../views/operator/operatorRouter';//

const originalPush = VueRouter.prototype.push

VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err)
}
Vue.use(VueRouter)


import test from './../views/operator/pages/test/pageA'
// comm:frees 
const Login = () => import('../views/Login.vue');


// admin:logins levels
const Admin = () => import('../views/Admin.vue');

// admin 
const Console = () => import('../views/Admin/Console.vue');
const Shop = () => import('../views/Admin/Shop.vue');
const Manage = () => import('../views/Admin/Manage.vue');
const Role   = () => import('../views/Admin/Role.vue');
const Work   = () => import('../views/Admin/Work.vue');


const routes = [
    // 
    {
        path: '/',
        component: Login,
        meta: {
            rbac: 'frees',
            title: ''
        }
    },
    {
        path: '/test',
        component: test,
        meta: {
            rbac: 'frees',
            title: 'test'
        }
    },
    {
        path: '/login',
        component: Login,
        meta: {
            rbac: 'frees',
            title: ''
        }
    },
    {
        path: '/admin',
        component: Admin,
        meta: {
            rbac: 'logins',
            title: ''
        },
        children: [
            {
                path: '/',
                component: Console,
                meta: {
                    rbac: 'logins',
                    title: ''
                }
            },
            // {
            //     path: 'shop_list/:menu_uuid',
            //     component: Shop,
            //     meta: {
            //         rbac: 'logins',
            //         title: ''
            //     }
            // },
            // {
            //     path: 'manage/:menu_uuid',
            //     component: Manage,
            //     meta: {
            //         rbac: 'logins',
            //         title: ''
            //     }
            // },
            // {
            //     path: 'manage_role/:menu_uuid',
            //     component: Role,
            //     meta: {
            //         rbac: 'logins',
            //         title: ''
            //     }
            // },
            // {
            //     path: 'shop_list/:menu_uuid',
            //     component: Shop,
            //     meta: {
            //         rbac: 'logins',
            //         title: ''
            //     }
            // },
            // {
            //     path: 'manage_role/:menu_uuid',
            //     component: Role,
            //     meta: {
            //         rbac: 'logins',
            //         title: ''
            //     }
            // },
            // {
            //     path: 'work/:menu_uuid',
            //     component: Work,
            //     meta: {
            //         rbac: 'logins',
            //         title: ''
            //     }
            // },
            ...operatorRouter.Admin
        ]
    }
];

const router = new VueRouter({
    // mode: 'history', // #
    // base: process.env.BASE_URL,
    routes
});


// ,
router.beforeEach((to, from, next) => {
    let login_token = lime.cookie_get('login_token');

    if (lime.debug) {
        console.log(login_token);
    }

    if (login_token == null || typeof login_token == 'undefined') {
        lime.cookie_set('login_token', null, 0);
        if (to.meta.rbac != 'frees') {
            next('/login');
        } else {
            next();
        }
    } else {
        // login_token
        lime.req('CheckLoginToken', {
            login_token: login_token,
            business_type: 'manage',
            login_type: 0
        }).then(res => {
            // cookie,2
            lime.cookie_set('login_token', login_token, 7200);
            // ,'/admin'
            if (to.path == '/') {
                next('/admin');
            } else {
                next();
            }
        }).catch(err => {
            lime.cookie_set('login_token', null, 0);
            // ,
            next('/login');
        })
    }
})

export default router

