<template>
    <!---->
    <div>
        <headMenus
                @onSearch="onSearch"
                @onRefresh="onRefresh"
                @handleAdd="handleAdd"
                @handleEdit="handleEdit"
                @handleDel="handleDel"
        ></headMenus>
        <hTable
                :list="list"
                :total="total"
                :table_des="table_des"
                :page_len="params.page_len"
                :currentPage="params.page_num"
                @currentChange="currentChange"
                @tableChoose="tableChoose"
                @sortChange="sortChange"
                :loading="loading"
        ></hTable>
        <formEditModal
                ref="formEditModal"
                title=""
                :form_des="form_des"
                @sure="sureSearch"
        ></formEditModal>
        <formEditModal
                ref="addTags"
                title=""
                width="50%"
                :form_des="add_tags_from_des"
                @sure="sureAddTags"
        ></formEditModal>
        <formEditModal

                ref="editTags"
                title=""
                width="50%"
                :form_des="edit_tags_from_des"
                @sure="sureEditTags"
        ></formEditModal>
    </div>


</template>

<script>
    import coneHttpTags from '../../_commom/connectorHttp/coneHttpTags'
    import headMenus from '../../_components/headMenus'
    import hTable from '../../_components/hTable'
    import formEditModal from '../../_components/formEditModal'
    import tableFromMix from '../../_commom/mixins/tableFromMix'
    import utils from "../../_commom/lib/utils";
    import pageEvent from "../../_commom/lib/pageEvent";

    export default {
        name: "tagGroup",
        mixins: [tableFromMix],
        components: {headMenus, hTable, formEditModal},
        data() {
            return {
                params: {


                    title: '',
                    order_field: null,
                    order_sort: '',
                    page_num: 1,
                    page_len: 20,

                },
                /**/
                tags_params: {
                    page_num: 1,
                    page_len: 50,

                },
                /**/
                tags_list:[],
                table_des: [
                    {sortable: false, label: '#', prop: '_list_index', width: '50'}
                    // {sortable:false,  label:'id'  ,   prop:'uuid'      ,     width:''}
                    , {sortable: false, label: '', prop: 'title', width: ''}
                    , {
                        sortable: false, label: '', prop: 'tag', width: '',

                        // render(scope) {
                        //
                        //     let row = scope.row;
                        //     let tag = row.tag;
                        //
                        //     let tags = tag.map(v=>{
                        //         return v.title
                        //     })
                        //     // return String(tags)
                        //     let str='';
                        //     if(tags.length){
                        //         str= '['+tags.join(' ] [')+']'
                        //     }else {
                        //         str=''
                        //     }
                        //
                        //     return str
                        // }}
                        render: (h, {column, index, row}) => {

                            let tag = row.tag;
                            let hs=[]

                            tag.map(v=>{
                                hs.push(
                                    h('el-tag',
                                        {
                                            attrs:{
                                                size:'small'
                                            }
                                            ,
                                            style:{
                                                margin:'2px'
                                            }
                                        },
                                        v.title)
                                )

                            })


                            return h(    // {String | Object | Function}
                                //  HTML 
                                // resolve  async 
                                'div',

                                // {Object}
                                //  attribute 
                                {

                                },

                                // {String | Array}
                                //  (VNodes) `createElement()` 
                                // 
                                hs

                            )
                        }
                    }
                    , {sortable: false, label: '', prop: 'remark', width: ''}


                ],
                form_des: [
                    {field: 'title', type: 'text', label: '', value: '',},
                ],
                /**/
                add_tags_from_des: [
                    {
                        field: 'title', type: 'text', label: '', value: '',
                        rules: [
                            {trigger: 'blur', required: true, message: ''}
                        ]
                    },
                    {
                        field: 'tag_uuids', type: 'checkbox', label: '', value: [],
                        handler:'loadMoreTags',placeholder:'',
                        options:[

                        ]

                    },
                    {field: 'remark', type: 'textarea', label: '', value: '',}
                ],
                edit_tags_from_des: [
                    {
                        field: 'title', type: 'text', label: '', value: '',
                        rules: [
                            {trigger: 'blur', required: true, message: ''}
                        ]
                    },
                    {
                        field: 'tag_uuids', type: 'checkbox', label: '', value: [],
                        handler:'loadMoreTags',placeholder:'',
                        options:[

                        ]

                    },
                    {field: 'remark', type: 'textarea', label: '', value: '',}
                ]
            }

        },
        methods: {
            /**/
            getList(params1) {


                this.loading = true;

                let params =params1|| this.params;
                return new Promise((resolve, reject) => {
                    coneHttpTags.ManageQuesGroupList(params)
                        .then(res => {
                            let data = res.data;


                            this.total = data.total / 1;
                            let rows = data.rows;

                            let page_len = params.page_len,
                                page_num = params.page_num;
                            let list = this._disposeList(rows, page_num, page_len);
                            this.loading = false;


                            // console.log('', this.list)
                            resolve(list)
                        })

                })
            },


            handleAdd() {
                this.$refs.addTags.show()

                if(!this.tags_list.length){

                    this.initTags()
                }
            },
            /**/
            sureAddTags(val) {

                let form_data = val.form_data;
                coneHttpTags.ManageQuesGroupAdd(form_data)
                    .then(res => {
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getList()
                            .then(list => {
                                this.list = list
                            })
                    })

            },
            /**/
            handleEdit() {
                let curr_list_one = this.curr_list_one || {};//

                let re = this.checkChooseListOne();
                if (!re) {
                    return false;
                }
                // title: "1556"
                // uuid: "4d769c3cea7bc3161fc6efcd77a3d846"
                let tag_uuids = curr_list_one.tag.map(v => {
                    return v.uuid;
                });
                curr_list_one.tag_uuids = tag_uuids;
                this.addDataFrom( //
                    this.edit_tags_from_des,
                    curr_list_one
                );

                this.$refs.editTags.show()
                if(!this.tags_list.length){

                    this.initTags()
                }

            },
            /**/
            sureEditTags(val) {
                let curr_list_one = this.curr_list_one;
                let uuid = curr_list_one.uuid;
                let form_data = val.form_data;
                coneHttpTags.ManageQuesGroupEdit({...form_data, uuid})
                    .then(res => {
                        this.$message({
                            message: '',
                            type: 'success'
                        });

                        this. onRefreshIndex(curr_list_one._index-1)
                    })
            },
            /**/
            handleDel() {
                //
                let curr_list_one = this.curr_list_one || {};//
                let re = this.checkChooseListOne();
                if (!re) {
                    return false;
                }

                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    coneHttpTags.ManageQuesGroupDel({uuid: curr_list_one.uuid})
                        .then(res => {
                            this.list.splice(curr_list_one._index - 1, 1)
                            this.$message({
                                type: 'success',
                                message: '!'
                            });
                        })

                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });

            }
            ,
            /**/
            getTags(title='') {

                let tags_params = this.tags_params
                // coneHttpTags.ManageQuesTagList({title,page_len: utils.getConfig('select_page_len')})


                if(this.tags_list.length< ((tags_params.page_num-1)*tags_params.page_len)){
                    return false;
                }
                coneHttpTags.ManageQuesTagList(tags_params)
                    .then(res => {
                        let data = res.data;
                        let rows = data.rows;
                        rows = rows.map(v => {
                            return {...v, label: v.title, value: v.uuid}
                        });


                        let tags_list = [ ...this.tags_list,...rows];
                        // let tags_list = [ ...rows];
                        this.tags_list = tags_list;



                        utils.editObjField(this.add_tags_from_des, {field: 'tag_uuids'}, {options : tags_list});
                        utils.editObjField(this.edit_tags_from_des, {field: 'tag_uuids'}, {options : tags_list});
                        // loading.close()
                    })
            },
            /**/
            initTags(){
                this.tags_params.page_num = 1;
                this.tags_list=[];
                this.getTags();

            },
            /**/
            loadMoreTags(val){
                this.tags_params.page_num++;
                this.getTags()


            }


        },
        mounted() {
            this.getList()
                .then(list => {
                    this.list = list;
                    this.initTags()
                })
            // this.$refs.formEditModal.show()

            /**/
            pageEvent.$off('loadMoreTags',)
            pageEvent.$on('loadMoreTags',(val)=>{

                this.loadMoreTags()


            })

        }
    }
</script>

<style scoped lang="less">

    @import "../../css/common";

</style>