/*

*/
<template>
    <div class="vehicle">
        <!--  -->
        <div style="height: 46px; line-height: 46px; overflow: hidden;">
            <el-row>
                <el-col :span="6">
                    <div style="padding-left:16px;">
                        <i class="el-icon-s-unfold"></i>
                        <span style="padding-left:9px;">
                            {{$store.state.AdminData.active_title}}
                        </span>
                    </div>
                </el-col>

                <el-col :span="18">
                    <div style="text-align: right; ">
                        <el-link @click="onSubMenu('onRefresh',true)" class="menu"></el-link>
                        <el-link @click="onSubMenu('onSearch',true)" class="menu"></el-link>

                        <el-link
                            class="menu" 
                            @click="onSubMenu(item)"
                            v-for="(item,index) in $store.state.AdminData.right_menus" 
                            :key="index">
                            {{item.name}}
                        </el-link>
                    </div>
                </el-col>
            </el-row>
        </div>
        <!--  -->
        <div style="border-top: solid 1px #f2f1f4;">
            <el-table 
                :data="rows"
                :height="height - 60 - 46 - 48"
                v-loading="loading"
                element-loading-text=""
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
                @sort-change="onSortChange"
                :highlight-current-row="true"
                @current-change="onSelectRow"
                style="width: 100%" 
                size="mini">
                
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="shop_name.cname" label=""></el-table-column>
                <el-table-column prop="vehicle_firm" label=""></el-table-column>
                <el-table-column prop="vehicle_vin" label=""></el-table-column>
                <el-table-column prop="vehicle_owner" label=""></el-table-column>
                <el-table-column prop="owner_tel" label=""></el-table-column>
                <el-table-column prop="market" label=""></el-table-column>
                <el-table-column prop="vehicle_type" label=""></el-table-column>
                <el-table-column prop="vehicle_weight" label="(kg)"></el-table-column>
                <el-table-column prop="transfer_weight" label="(kg)"></el-table-column>
                <el-table-column prop="outline_long" label="mm"></el-table-column>
                <el-table-column prop="outline_wide" label="mm"></el-table-column>
                <el-table-column prop="outline_high" label=""></el-table-column>
                <el-table-column prop="inline_long" label="mm"></el-table-column>
                <el-table-column prop="inline_wide" label="mm"></el-table-column>
                <el-table-column prop="inline_high" label="mm"></el-table-column>
                <el-table-column prop="axle_num" label=""></el-table-column>
                <el-table-column prop="tyre_num" label=""></el-table-column>
                <el-table-column prop="tyre_specs" label=""></el-table-column>
                <el-table-column prop="vehicle_delivery_time" label=""></el-table-column>
                <el-table-column prop="business_scope" label=""></el-table-column>
                <el-table-column prop="vehicle_color" label=""></el-table-column>
                <!-- <el-table-column prop="pay_type" label=""></el-table-column> -->
                <el-table-column prop="pay_type" label=""></el-table-column>
                <!-- <el-table-column prop="insurance_buy_time" label=""></el-table-column> -->
                <el-table-column prop="insurance_buy_time" label=""></el-table-column>
                <el-table-column prop="insurance_expire_time" label=""></el-table-column>
                <el-table-column prop="vehicle_checkout_time" label=""></el-table-column>
                <el-table-column prop="payment_time" label=""></el-table-column>
                <el-table-column prop="payment_expire_time" label=""></el-table-column>
                <el-table-column prop="road_business_license" label=""></el-table-column>
                <el-table-column prop="business_type" label=""></el-table-column>
                <el-table-column prop="inline_type" label=""></el-table-column>
                <el-table-column prop="vehicle_checkout_time" label=""></el-table-column>
                <el-table-column prop="add_time" label=""></el-table-column>
            </el-table>
            
            <div class="page" :style="{width:width - 250 + 'px'}">
                <el-pagination
                    :current-page.sync="SearchFormData.page_num"
                    @current-change="onPageChange"
                    layout="prev, pager, next, jumper"
                    :total="total">
                </el-pagination>
            </div>
        </div>
        <!--  -->
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="search_show"
            width=400px>
            <el-form :model="SearchFormData" label-width="80px" size="small">
                <el-form-item label="">
                    <el-input v-model="SearchFormData.shop_name"/>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="SearchFormData.vehicle_vin"/>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="search_show = false" size="small"> </el-button>
                <el-button type="primary" @click="onSearchSubmit" size="small"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="edit_show"
            width=400px>
            <el-form :model="EditFormData" label-width="80px" size="small">
                <el-form-item label="" prop="tag_name">
                    <el-input v-model="EditFormData.tag_name"/>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="edit_show = false" size="small"> </el-button>
                <el-button type="primary" @click="onEditSubmit" size="small"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="add_show"
            width=400px>
            <el-form :model="AddFormData" :rules="AddFormRule" label-width="80px" size="small">
                <el-form-item label="" prop="tag_name">
                    <el-input v-model="AddFormData.tag_name"/>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="add_show = false" size="small"> </el-button>
                <el-button type="primary" @click="onAddSubmit" size="small"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <!-- <el-dialog
            title=""
            :visible.sync="detail_show"
            width=750px>

            <span slot="footer">
                <el-button type="primary" @click="detail_show = false" size="small"> </el-button>
            </span>
        </el-dialog> -->
        <el-drawer
            :visible.sync="detail_show"
            size="40%"
            title="">
            <!-- <h3 class="drawer-title"></h3> -->
            <div class="drawer-body">
                <ul class="vehicle-ul">
                    <li>
                        <div><span></span><span>{{DetailFormData.shop_name != null || DetailFormData.shop_name != undefined ? DetailFormData.shop_name.cname : ''}}</span></div>
                        <div><span></span><span>{{DetailFormData.vehicle_firm}}</span></div>
                    </li>
                    <li>
                        <div><span></span><span>{{DetailFormData.vehicle_vin}}</span></div>
                        <div><span></span><span>{{DetailFormData.vehicle_owner}}</span></div>
                    </li>
                    <li>
                        <div><span></span><span>{{DetailFormData.owner_tel}}</span></div>
                        <div><span></span><span>{{DetailFormData.market}}</span></div>
                    </li>
                    <li>
                        <div><span></span><span>{{DetailFormData.vehicle_type}}</span></div>
                        <div><span>(kg)</span><span>{{DetailFormData.vehicle_weight}}</span></div>
                    </li>
                    <li>
                        <div><span>(kg)</span><span>{{DetailFormData.transfer_weight}}</span></div>
                        <div><span>(mm)</span><span>{{DetailFormData.outline_long}}</span></div>
                    </li>
                    <li>
                        <div><span>(mm)</span><span>{{DetailFormData.outline_wide}}</span></div>
                        <div><span></span><span>{{DetailFormData.outline_high}}</span></div>
                    </li>
                    <li>
                        <div><span>(mm)</span>{{DetailFormData.inline_long}}<span></span></div>
                        <div><span>(mm)</span>{{DetailFormData.inline_wide}}<span></span></div>
                    </li>
                    <li>
                        <div><span>(mm)</span>{{DetailFormData.inline_high}}<span></span></div>
                        <div><span></span><span>{{DetailFormData.axle_num}}</span></div>
                    </li>

                    <li>
                        <div><span></span><span>{{DetailFormData.tyre_num}}</span></div>
                        <div><span></span><span>{{DetailFormData.tyre_specs}}</span></div>
                    </li>
                    <li>
                        <div><span></span><span>{{DetailFormData.vehicle_delivery_time}}</span></div>
                        <div><span></span><span>{{DetailFormData.business_scope}}</span></div>
                    </li>
                    <li>
                        <div><span></span><span>{{DetailFormData.vehicle_color}}</span></div>
                        <div><span></span><span>{{DetailFormData.pay_type}}</span></div>
                    </li>
                    <li>
                        <div><span></span><span>{{DetailFormData.insurance_buy_time}}</span></div>
                        <div><span></span><span>{{DetailFormData.insurance_expire_time}}</span></div>
                    </li>
                    <li>
                        <div><span></span><span>{{DetailFormData.vehicle_checkout_time}}</span></div>
                        <div><span></span><span>{{DetailFormData.payment_time}}</span></div>
                    </li>
                    <li>
                        <div><span></span><span>{{DetailFormData.payment_expire_time}}</span></div>
                        <div><span></span><span>{{DetailFormData.road_business_license}}</span></div>
                    </li>

                    <li>
                        <div><span></span><span>{{DetailFormData.business_type}}</span></div>
                        <div><span></span><span>{{DetailFormData.inline_type}}</span></div>
                    </li>
                    <li>
                        <div><span></span><span>{{DetailFormData.vehicle_checkout_time}}</span></div>
                        <div><span></span><span>{{DetailFormData.add_time}}</span></div>
                    </li>
                    
                </ul>
            </div>
            <div class="drawer-btn">
                <el-button type='danger' @click="detail_show = false" size="small" style="display: block;margin: 30px auto;"></el-button>
            </div>
        </el-drawer>
    </div>
</template>

<script>
import { cookie_get } from "../../_commom/connectorHttp/trainingResourcesHttp.js";
import { ManageVehExtendList } from "../../_commom/connectorHttp/vehicleEquipmentHttp.js";
import { ManageVehExtendDetail } from "../../_commom/connectorHttp/vehicleEquipmentHttp.js";

import store from "@/store";
import util from "@/util.js";
export default {
    data () {
        return {
            // 
            rows:[],
            total:0,
            loading:false,
            curr_row:null,
            // 
            // 
            search_show: false,
            SearchFormData: {
                shop_name: '',//
                vehicle_vin: '',//
                page_num: 1,// 
                page_len: 10,//
                order_field:'add_time',//
                order_sort:'desc'//
            },
            // 
            edit_show: false,
            EditFormData: {
                tag_name: '',
            },
            // 
            add_show: false,
            AddFormData: {
                tag_name: ''
            },
            AddFormRule: {
                tag_name: [
                    { required: true, message: '', trigger: 'blur' },
                ]
            },
            // 
            detail_show: false,
            DetailFormData: {}
        }
    },
    created () {
        this.init();
    },
    computed:{
        width:() => {
            return store.state.AppData.width;
        },
        height:() => {
            return store.state.AppData.height;
        },
        upload_url:() => {
            return store.state.AppData.upload_url;
        },
        img_host:() => {
            return store.state.AppData.img_host;
        }
    },
    methods: {
        //  menu: local
        onSubMenu(menu, local = false) {
            util.submenu(menu,this,cookie_get (), local);
        },

        // 
        //  init
        init () {
            this.loading = true;
            var dataList = {
                shop_name: this.SearchFormData.shop_name,
                vehicle_vin: this.SearchFormData.vehicle_vin,
                page_num: this.SearchFormData.page_num,// 
                page_len: this.SearchFormData.page_len,//
                order_field: this.SearchFormData.order_field,//
                order_sort: this.SearchFormData.order_sort,//
                login_token: cookie_get()
            }
            ManageVehExtendList (dataList).then(res => {
                if (res.code == 0) {
                    this.rows = res.data.rows;
                    this.total = res.data.total;
                } else {
                    this.$message.error(res.msg);
                }
                this.loading = false;
            })
            // 
            setTimeout(() => {
                this.loading = false;
            }, 10000);
        },
        // 
        onSortChange (column) {
            this.SearchFormData.order_field = column.prop;
            if (column.order == 'ascending') {
                this.SearchFormData.order_sort  = 'asc';
            } else {
                this.SearchFormData.order_sort  = 'desc';
            }
            this.init();
        },
        // 
        onSelectRow (row) {
            this.curr_row = row;
        },
        // 
        onPageChange (page) {
            this.SearchFormData.page_no = page;
            this.init();
        },

        // 
        // 
        onRefresh () {
            this.init();
        },
        // 
        onSearch () {
            this.search_show = true;
        },
        // 
        onSearchSubmit () {
            this.SearchFormData.page_num = 1;
            this.init();
            this.search_show = false;
            this.SearchFormData.shop_name = '';
            this.SearchFormData.vehicle_vin = '';
        },
        // 
        handleEdit () {
            if (util.empty(this.curr_row)) {
                this.$message.error('');
                return;
            }
            this.EditFormData.tag_name = this.curr_row.tag_name;
            this.edit_show = true;
        },
        // 
        onEditSubmit () {
            var EditData = {
                tag_name: this.EditFormData.tag_name,
                uuid: this.curr_row.uuid,
                login_token: cookie_get()
            }
            ManageResourceTagEdit (EditData).then(res => {
                if (res.code == 0) {
                    this.init();
                } else {
                    this.$message.error(res.msg);
                }
                this.edit_show = false;
            })
        },
        // 
        handleAdd () {
            this.add_show = true;
        },
        // 
        onAddSubmit () {
            var AddData = {
                tag_name: this.AddFormData.tag_name,
                login_token: cookie_get()
            }
            ManageResourceTagAdd (AddData).then(res => {
                if (res.code == 0) {
                    this.$message.success('');
                    this.init();
                } else {
                    this.$message.error(res.msg);
                }
                this.add_show = false;
            })
        },
        // 
        handleDel () {
            if (util.empty(this.curr_row)) {
                this.$message.error('');
                return;
            }
            this.$confirm('','').then(() => {
                var DelData = {
                    uuid: this.curr_row.uuid,
                    login_token: cookie_get()
                }
                ManageResourceTagDel (DelData).then(res => {
                    if (res.code == 0) {
                        this.$message.success('');
                        this.init();
                    } else {
                        this.$message.error(res.msg);
                    }
                })
            })
        },
        // 
        handleDetail () {
            if (util.empty(this.curr_row)) {
                this.$message.error('');
                return;
            }
            var DetailData = {
                uuid: this.curr_row.uuid,
                login_token: cookie_get()
            }
            ManageVehExtendDetail (DetailData).then(res => {
                this.DetailFormData = res.data;
                this.detail_show = true;
            })
        }
        
    }

}
</script>

<style lang="less">
    .vehicle{
        .menu{
            display: inline-block;
            padding:0 16px;
            text-align: center;
        }
        .page {
            height: 40px; 
            line-height: 40px; 
            text-align: right;
            position: fixed;
            bottom: 0;
            right:0;
            overflow: hidden;
        }
        .drawer-body{
            ul{
                list-style: none;
                li{
                    overflow: hidden;
                    div:nth-of-type(1){
                        float: left;
                        margin-left: 30px;
                    }
                    div:nth-of-type(2){
                        float: right;
                        margin-right: 30px;
                    }
                    div{
                        span:nth-of-type(1){
                            width: 130px;
                            text-align: right;
                            display: inline-block;
                            font-size: 14px;
                        }
                        span:nth-of-type(2){
                            width: 200px;
                            text-align: left;
                            display: inline-block;
                            font-size: 14px;
                        }
                    }
                }
            }
        }
        .el-drawer__body{
            overflow: auto !important;
        }
    }
</style>