import lime from "../../../../lime";

import qs from 'qs'
console.log(qs,'---------------------------------------')
import util from "../lib/utils";




export default class Http {

    constructor() {

    }
    static loading  = null;

    /*loading*/
    static endLoad(){
        // this.loading.close();
    }

    static common_header = {
        // 'Content-Type' : 'application/x-www-form-urlencoded;charset=UTF-8'
    }; //
    static common_data = {
        login_token:lime.cookie_get('login_token'),
        // 'Content-Type':'application/x-www-form-urlencoded'
    };   //
    /**
     * @description   
     * @param  params 
     * @return Object 
     * */
    static _disposeParams(params = {}) {
        let new_params = {};
        for (let i in params) {

            let item = params[i];
            if (!(item === '' || item === undefined || item === null)) {
                if (typeof item ==="number" ){
                    item = item+''
                }
                new_params[i] = item;
            }

        }
        return new_params

    }


    /**
     * @description 
     * @param _module 
     * @param params 
     * @param headers 
     * */
    static httpPost(_module, params={}, headers = {}) {





        params = this._disposeParams(params);
        Object.assign(params,this.common_data);
        Object.assign(headers,this.common_header);
        console.info('', _module, params  , headers  )
        // console.info('', _module,qs.stringify(params)   , headers  )
        return new Promise((resolve, reject) => {
            // params = qs.stringify(params);
            // console.log('params',params)
            lime.req(_module, params, headers)
                .then(res => {
                    console.log('',res)
                    resolve(res)

                    this.endLoad()
                })
                .catch(err=>{
                    console.log('',err)


                    util.Message.error(JSON.stringify(err))
                    reject(err);

                })
        })



    }


}