<template>
    <div class="shopList h100 posi-relative">
        <!--        <areaChoose></areaChoose>-->
        <headMenus
                @onSearch="onSearch"
                @onRefresh="onRefresh"
                @handleAdd="handleAdd"
                @handleEdit="handleEdit"
                @handleDel="handleDel"
                @handleDetail="handleDetail"
                @handleAddSup="handleAddSup"
                @handleExamine="handleExamine"
                @handleReset="handleReset"
        ></headMenus>
        <!--        <areaChoose></areaChoose>-->
        <hTable
                :list="list"
                :total="total"
                :table_des="table_des"
                :page_len="params.page_len"
                :currentPage="params.page_num"
                @currentChange="currentChange"
                @tableChoose="tableChoose"
                @sortChange="sortChange"
                :loading="loading"
        ></hTable>
        <formEditModal
                ref="formEditModal"
                title=""
                width="30%"
                :form_des="form_des"
                @sure="sureSearch"
        ></formEditModal>
        <formEditModal
                ref="addSuperAdmin"
                title=""
                width="30%"
                :form_des="super_admin_from_des"
                @sure="sureAddSuperAdmin"
        ></formEditModal>
        <formEditModal
                ref="resetSuperAdmin"
                title=""
                width="30%"
                :form_des="super_admin_reset_from_des"
                @sure="sureResetSuperAdmin"
        ></formEditModal>
        <fromEditDrawer
                ref="addShop"
                title=""
                width="50%"
                :form_des="shop_from_des"
                @sure="sureAddShop"
        ></fromEditDrawer>
        <formEditModal
                ref="editShop"
                title=""
                width="50%"
                :form_des="shop_edit_from_des"
                @sure="sureEditShop"

        ></formEditModal>
        <showDetailModal ref="showDetailModal" title=""></showDetailModal>
    </div>


</template>

<script>
    import headMenus from '../../_components/headMenus'
    import hTable from '../../_components/hTable'
    import formEditModal from '../../_components/formEditModal'
    import fromEditDrawer from '../../_components/fromEditDrawer'
    import showDetailModal from '../../_components/showDetailModal'
    import areaChoose from '../../_components/areaChoose'
    import coneHttpShop from '../../_commom/connectorHttp/coneHttpShop'

    import tableFromMix from '../../_commom/mixins/tableFromMix'
    import utils from '../../_commom/lib/utils'
    import pageEvent from "../../_commom/lib/pageEvent";

    console.log('tableFromMix',tableFromMix)
    export default {
        name: "shopList",
        components: {headMenus, hTable, formEditModal, fromEditDrawer, showDetailModal},
        mixins: [tableFromMix],
        data() {

            let self = this;



            return {
                //
                params: {

                    name: '', //
                    state: '',// 0.,1.,-1.
                    prov_uuid: '',//
                    city_uuid: '',//
                    area_uuid: '',//
                    addr: '',//
                    local: '',//
                    range: '',//
                    is_pause: '',// 0:,1.
                    is_work_day: '',// 1.,0.
                    is_now_work: '',// 0.,1.

                    order_field: null,
                    order_sort: '',
                    page_num: 1,
                    page_len: 20,

                },
                table_des: [
                    {sortable: false, label: '#', prop: '_list_index', width: '50'}
                    // , {sortable: false, label: 'lat', prop: 'lat', width: ''}
                    // , {sortable: false, label: 'lng', prop: 'lng', width: ''}

                    , {sortable: false, label: '', prop: 'name', width: ''}
                    // , {sortable: false, label: '', prop: 'remark', width: ''}
                    // , {sortable: false, label: '', prop: 'detail', width: ''}
                    , {sortable: false, label: '', prop: 'addr', width: ''}
                    , {sortable: false, label: '', prop: 'contact_name', width: ''}
                    , {sortable: false, label: '', prop: 'contact_tel', width: ''}
                    // , {sortable: false, label: '', prop: 'day_start_time', width: ''}
                    // , {sortable: false, label: '', prop: 'day_end_time', width: ''}
                    // , {sortable: false, label: '', prop: 'week_work_day', width: ''}
                    // ,{sortable:false,  label:''  ,   prop:'state'      ,     width:'',
                    //     filters:[
                    //         { text: '', value: '0' },
                    //         { text: '', value: '1' },
                    //         { text: '', value: '-1' },
                    //     ]
                    // }
                    , {
                        sortable: false, label: '', prop: 'state', width: '',



                        // },
                        render: (h, {column, index, row}) => {
                            // console.log(row.state)
                            // this.$forceUpdate()

                            let obj = utils.getFieldDesConf('shop', 'state');
                            // console.log(obj)
                            // return obj[row.operate_state + ''];
                            return h('div', obj[row.state + ''].name)
                    /*        return h(
                                // {String | Object | Function}
                                //  HTML 
                                // resolve  async 
                                'el-switch',

                                // {Object}
                                //  attribute 
                                {
                                    style: {},
                                    attrs: {
                                        // 'name':false,
                                        // 'nameA':row.state,
                                        // 'nameb':row.state === 1,
                                        'active-value': '1',
                                        'inactive-value': '-1',
                                        'active-text': 'ON',
                                        'inactive-text': 'OFF',
                                        'value': row.state + ''
                                    },
                                    on: {
                                        change(val) {
                                            self.switchChange(val, row)
                                        }
                                    },
                                },

                                // {String | Array}
                                //  (VNodes) `createElement()` 
                                // 
                                // [
                                //     '',
                                //     h('h1',{
                                //         style: {
                                //             color: 'blue',
                                //
                                //         },
                                //     }, ''),
                                //
                                // ],

                            )*/
                        }
                    },
                    {
                        sortable: false, label: '', prop: 'operate_state', width: '',
                        render(h, {column, index, row}) {

                            let obj = utils.getFieldDesConf('shop', 'operate_state');
                            // console.log(obj)
                            // return obj[row.operate_state + ''];
                            return h('div', obj[row.operate_state + ''].name)

                        }

                    }
                    // , {sortable: 'custom', label: '', prop: 'name', width: ''}
                    , {sortable: 'custom', label: '', prop: 'add_time', width: 'auto'}

                ],

                /**/
                form_des: [
                    {
                        field_type: 'composite',
                        type: 'address',
                        label: '',
                        field: 'lng',
                        field_arr: [
                            {field: 'prov_uuid', value: ''},
                            {field: 'city_uuid', value: ''},
                            {field: 'area_uuid', value: ''},
                        ]
                    },
                    {field: 'addr', type: 'text', label: '', value: ''},
                    {field: 'name', type: 'text', label: '', value: ''},
                    {
                        field: 'state', type: 'radio', label: '', value: '', options:
                            [
                                {name: '', value: ''},
                                {name: '', value: '0'},
                                {name: '', value: '1'},
                                {name: '', value: '-1'}
                            ]
                    },
                    /*{field: 'range', type: 'number', label: '', value: ''},
                    {
                        field: 'is_pause', type: 'radio', label: '', value: '', options:
                            [{name: '', value: ''}, {name: '', value: '0'}, {name: '', value: '1'}]
                    },
                    {
                        field: 'is_work_day', type: 'radio', label: ' ', value: '', options:
                            [
                                {name: '', value: ''},
                                {name: '', value: '0'},
                                {name: '', value: '1'},
                            ]
                    },
                    {
                        field: 'is_now_work', type: 'radio', label: '', value: '', options:
                            [
                                {name: '', value: ''},
                                {name: '', value: '0'},
                                {name: '', value: '1'},
                            ]
                    },*/
                ],

                /**/
                super_admin_from_des: [
                    // {field: 'shop_uuid', type: 'hidden', label: '',
                    //     value:  this,
                    //     value1:  this.super_admin_from_des,
                    // },
                    {
                        field: 'name', type: 'text', label: '', value: '',
                        rules: [{required: true, message: '', trigger: 'blur'},]
                    },
                    {
                        field: 'mobile', type: 'tel', label: '', value: '',
                        rules: [
                            {trigger: 'blur', required: true, validator: utils.telCheck}
                        ]
                    },
                    {
                        field: 'pwd', type: 'password', label: '', value: '',
                        rules: [{
                            required: true, trigger: 'blur', validator: (rule, value, callback) => {

                                if (!value) {
                                    callback(new Error(''));
                                } else {
                                    let re = utils.checkParams(value, 'pwd')
                                    if (!re.is_check) {
                                        callback(new Error(re.msg));
                                    } else {
                                        callback();
                                    }
                                }
                            }

                        }
                        ]
                    }
                ],
                /**/
                super_admin_reset_from_des: [


                    {
                        field: 'pwd', type: 'password', label: '', value: '',
                        rules: [{
                            required: true, trigger: 'blur', validator: (rule, value, callback) => {

                                if (!value) {
                                    callback(new Error(''));
                                } else {
                                    let re = utils.checkParams(value, 'pwd')
                                    if (!re.is_check) {
                                        callback(new Error(re.msg));
                                    } else {
                                        callback();
                                    }
                                }
                            }

                        }
                        ]
                    }
                ],
                /*      /!**!/
                      add_shop_params: {//
                          name: '',
                          face: '',
                          remark: '',
                          addr: '',
                          prov_uuid: '',
                          city_uuid: '',
                          area_uuid: '',
                          lng: '',
                          lat: '',
                          parent_uuid: '',//required
                          contact_name: '',//required
                          contact_tel: '',//required

                      },*/
                /**/
                shop_from_des: [
                    {
                        field: 'name', type: 'text', label: '', value: '',
                        rules: [{required: true, message: '', trigger: 'blur'},]
                    },
                    {
                        field: 'contact_name', type: 'text', label: '', value: '',
                        rules: [{required: true, message: '', trigger: 'blur'},]
                    },
                    {
                        field: 'contact_tel', type: 'tel', label: '', value: '',
                        rules: [
                            {trigger: 'blur', required: true, validator: utils.telCheck}
                        ]
                    },
                    {field: 'parent_uuid', type: 'searchSelect', label: '', value: '', options: []},
                    {field: 'face', type: 'upload', label: '', value: ''},
                    {
                        field_type: 'composite',
                        type: 'address',
                        label: '',
                        field: 'lng',
                        field_arr: [
                            {field: 'prov_uuid', value: ''},
                            {field: 'city_uuid', value: ''},
                            {field: 'area_uuid', value: ''},
                        ]
                    },
                    {field: 'addr', type: 'textarea', label: '', value: ''},
                    {field: 'remark', type: 'textarea', label: '', value: ''},

                    // {field: 'prov_uuid', type: 'text', label: '', value: ''},
                    // {field: 'city_uuid', type: 'text', label: '', value: ''},
                    // {field: 'area_uuid', type: 'text', label: '', value: ''},

                    // {
                    //     field_type: 'composite',
                    //     type: 'map',
                    //     label: '',
                    //     field: 'lng',
                    //     field_arr: [
                    //         {field: 'lng', value: '',},
                    //         {field: 'lat', value: '',}
                    //     ]
                    // }
                ],
                // 
                /*   shop_edit_params: {
                       uuid: '',
                       name: '',
                       contact_name: '',
                       contact_tel: '',

                       remark: '',
                       addr: '',
                       prov_uuid: '',
                       city_uuid: '',
                       area_uuid: '',
                       'lng': '',
                       'lat': '',
                   },*/
                /**/
                shop_edit_from_des: [
                    // {field: 'uuid', type: 'text', label: 'uuid', value: ''},
                    // {field: 'uuid', type: 'searchSelect', label: '', value: '',options:[]},
                    {
                        field: 'name', type: 'text', label: '', value: '',
                        rules: [{required: true, message: '', trigger: 'blur'},]
                    },
                    {
                        field: 'contact_name', type: 'text', label: '', value: '',
                        rules: [{required: true, message: '', trigger: 'blur'},]
                    },
                    {
                        field: 'contact_tel', type: 'tel', label: '', value: '',
                        rules: [
                            {trigger: 'blur', required: true, validator: utils.telCheck}
                        ]
                    },
                    {field: 'face', type: 'upload', label: '', value: ''},
                    {
                        field_type: 'composite',
                        type: 'address',
                        label: '',
                        field: 'lng',
                        field_arr: [
                            {field: 'prov_uuid', value: ''},
                            {field: 'city_uuid', value: ''},
                            {field: 'area_uuid', value: ''},
                        ]
                    },


                    {field: 'addr', type: 'text', label: '', value: ''},
                    {field: 'remark', type: 'textarea', label: '', value: ''},
                    // {field: 'prov_uuid', type: 'text', label: '', value: ''},
                    // {field: 'city_uuid', type: 'text', label: '', value: ''},
                    // {field: 'area_uuid', type: 'text', label: '', value: ''},
                    // {
                    //     field_type: 'composite',
                    //     type: 'map',
                    //     label: '',
                    //     field_arr: [
                    //         {field: 'lng', value: '',},
                    //         {field: 'lat', value: '',}
                    //     ]
                    // }


                ],
            }

        },
        methods: {
            /**/
            handleAdd() {
                this.$refs.addShop.show()

            },
            /**/
            sureAddShop(val) {

                coneHttpShop.ManageAddShop(val.form_data)
                    .then(res => {
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getList().then(list=>{
                            this.list=list;
                        })
                    })
                    .catch(err => {
                        // this.$message.error(err.msg);
                    })
            },
            /**/
            handleEdit() {
                //
                let curr_list_one = this.curr_list_one || {};//
                let index = curr_list_one._index;
                let re = this.checkChooseListOne();
                if (!re) {
                    return false;
                }
                this.addDataFrom( //
                    this.shop_edit_from_des,
                    this.curr_list_one
                );


                console.log('this.shop_edit_from_des', this.shop_edit_from_des);

                this.$refs.editShop.show();


            },
            /**/
            sureEditShop(val) {
                console.log(val)
                // return false;
                let curr_list_one = this.curr_list_one;
                let uuid = curr_list_one.uuid;

                coneHttpShop.ManageShopEdit({...val.form_data, uuid})
                    .then(res => {
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.onRefreshIndex(curr_list_one._index-1)

                    })
                    .catch(err => {
                        // this.$message.error(err.msg);
                    })
            },

            /**/
            handleDel() {
                //
                let curr_list_one = this.curr_list_one || {};//
                let re = this.checkChooseListOne();
                if (!re) {
                    return false;
                }

                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    coneHttpShop.ManageShopDel({uuid: curr_list_one.uuid})
                        .then(res => {
                            this.list.splice(curr_list_one._index - 1, 1)
                            this.$message({
                                type: 'success',
                                message: '!'
                            });
                        })

                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });

            }
            ,
            handleDetail() {
                let curr_list_one = this.curr_list_one || {};//
                let index = curr_list_one._index;
                let re = this.checkChooseListOne();
                if (!re) {
                    return false;
                }
                coneHttpShop.ManageShopDetail({uuid: this.curr_list_one.uuid})
                    .then(res => {
                        let data = res.data;
                        this.$refs.showDetailModal.show([
                            {name: '', value: data.name},
                            {name: '', value: data.face, type: 'image'},
                            {name: '', value: data.remark},
                            {name: '', value: data.detail},
                            {name: '', value: data.prov_name + data.city_name + data.area_name + data.addr},
                            {name: '', value: data.contact_name},
                            {name: '', value: data.contact_name},
                            {name: '', value: data.contact_tel},
                            {name: '', value: utils.getFieldDesConf('shop', 'state')[data.state].name},
                            {
                                name: '',
                                value: utils.getFieldDesConf('shop', 'operate_state')[data.operate_state].name
                            },
                            {name: '', value: data.day_start_time},
                            {name: '', value: data.day_end_time},
                            {name: '', value: data.week_work_day},
                            {name: '', value: utils.getFieldDesConf('shop', 'is_pause')[data.is_pause].name},
                            {name: '', value: data.pause_start_date},
                            {name: '', value: data.pause_end_date},
                            {name: '', value: data.add_time},
                            {name: 'ip', value: data.add_ip},
                            {name: '', value: data.last_time},
                            {name: '', value: data.acc_mobile},

                        ],'drawer')

                    })


            },
            /**/
            getList(params1) {
                this.loading = true;

                let params =params1|| this.params;
                return new Promise((resolve, reject) => {
                    coneHttpShop.getMemberShopList(params)
                        .then(res => {
                            let data = res.data;


                            this.total = data.total / 1;
                            let rows = data.rows;

                            let page_len = params.page_len,
                                page_num = params.page_num;
                            let list = this._disposeList(rows, page_num, page_len);
                            this.loading = false;


                            // console.log('', this.list)
                            resolve(list)
                        })

                })


            },


            /* */
            handleAddSup() {

                let curr_list_one = this.curr_list_one || {};//

                let re = this.checkChooseListOne();
                if (!re) {
                    return false;
                }
                this.$refs.addSuperAdmin.show()

            },
            /**/
            sureAddSuperAdmin(val) {

                let params = val.form_data;

                //
                let curr_list_one = this.curr_list_one;//


                coneHttpShop.ManageCreateShopAdmin({...params, shop_uuid: curr_list_one.uuid})
                    .then(res => {

                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    })
                    .catch(err => {
                        // this.$message.error(err.msg);
                    })
            }
            /**/,
            handleReset() {


                let re = this.checkChooseListOne();
                if (!re) {
                    return false;
                }
                this.$refs.resetSuperAdmin.show()
            }
            // 
            , sureResetSuperAdmin(val) {
                console.log(val)
                let data = val.form_data;
                let curr_list_one = this.curr_list_one;//

                coneHttpShop.ManageShopAdminResetPwd({new_pwd: data.pwd, uuid: curr_list_one.uuid})
                    .then(res => {
                        this.$message({
                            type: 'success',
                            message: ''
                        });
                    })
            }
            /**/
            , handleExamine(row) {

                let curr_list_one = this.curr_list_one;
                let curr_one ={ };

                if(Object.keys(row).length){
                    curr_one= row
                }else {
                    if(this.checkChooseListOne()){
                        curr_one = curr_list_one ;
                    }else {
                        return false;
                    }
                }


                let _index = curr_one._index;
                let uuid = curr_one.uuid;


                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    coneHttpShop.ManageShopCheck({uuid: uuid})
                        .then(res => {

                            this.onRefreshIndex( _index -1)


                        })
                })
                .catch(()=>{

                })

            }

            /**/
            , switchChange(val, row) {

                this.handleExamine(row)


            }
            /**/
            , getShopList(val) {
                console.log('', val)
                coneHttpShop.getMemberShopList({
                    name: val.value,
                    page_num: 1,
                    page_len: utils.getConfig('select_page_len')
                })
                    .then(res => {

                        let list = res.data.rows;
                        let options = list.map(v => {
                            return {name: v.name, value: v.uuid}
                        })
                        utils.editObjField(val.from_des, {field: 'parent_uuid'}, {options});
                    });


            }
            ,
            /**/
            uploadFaceSuccess(val) {
                console.log(val)
                let value = val.value;
                let from_des = val.from_des;
                utils.editObjField(from_des, {field: 'face'}, {value});

            }
        },
        mounted() {
            this.getList()
                .then(list => {
                    this.list = list
                })
            // this.$refs.formEditModal.show()
            // console.log(utils.checkParams('17333043302','tel'))
            pageEvent.$off('getShopList', null);
            pageEvent.$on('getShopList', this.getShopList);
            pageEvent.$off('uploadFaceSuccess', null);
            pageEvent.$on('uploadFaceSuccess', this.uploadFaceSuccess);
        }
    }
</script>

<style scoped lang="less">

    @import "../../css/common";

</style>e>