/*
* 
* */

import util from '../../../../util'

export default {
    data() {
        return {
            list: [],
            curr_list_one:{},//

            total: 0,
            loading: true
        }
    },


    methods:{

        /**/
        addDataFrom(form_des, data){
            // console.log(form_des)
            for(let i = 0,len=form_des.length;i<len;i++){
                let field = form_des[i].field;
                let field_type = form_des[i].field_type;

                if( field_type==='composite'){
                    if(data[field] !==undefined){
                        form_des[i].value = data[field];
                    }
                    let field_arr = form_des[i].field_arr;


                    for(let j = 0,len=field_arr.length;j<len;j++){


                        let field1 = field_arr[j].field;
                        if(data[field1]!==undefined){
                            field_arr[j].value=data[field1]
                        }



                    }
                }else {
                    if(data[field] !==undefined){
                        form_des[i].value = data[field];
                    }
                }

            }


        },

        /*   */
        _disposeList(list,page_index,page_size){

            let re = [];
            list.forEach((v,k)=>{
                re.push({...v,_index:k+1,_list_index:k+(page_size *(page_index-1) )+1})
            })
            return re;
        },



        /*table */
        currentChange(page_index) {

            this.params.page_num = page_index;
            this.getList()
                .then(list=>{
                    this.list=list
                })

        },
        /**/
        sortChange(val) {

            this.params.order_field = val.field;
            this.params.order_sort = val.type;
            this.getList()
                .then(list=>{
                    this.list=list
                })

        },
        // /*params  */
        clearSearchParams(){
            // this.clearFromData(params);
            this.addDataFrom(this.form_des,this.params)

        },
        /*  */
        clearFromData( params,init_params={}) {
            for (let i in params){
                params[i] = init_params[i]||''
            }
            this.initParams()


        },
        /**/
        initParams(){
            this.params.order_field = '';
            this.params.order_sort = '';
            this.params.page_num =1;
            this.params.page_len =20;
        },
        /* */
        initPage(){
            this.initParams()
            this.getList()
                .then(list=>{
                    this.list=list
                })
        },




        /* 0*/
        onRefreshIndex(index){
            let params = Object.assign({},this.params);
            console.log(params.page_num,params.page_len,index)
            let page_num = ((params.page_num-1) * params.page_len) +index+1;
            let page_len = 1
            params.page_num=page_num;
            params.page_len=page_len;
            this.getList(params)
                .then(list=>{
                    this.list.splice(index,1,list[0])
                })




        },
        /**/
        onRefresh() {

            // this.clearFromData(this.params); // param
            // this.clearSearchParams();

            this.getList()
        },
        /**/
        onSearch() {
            console.log(' ');

            this.clearSearchParams();
            this.clearFromData(this.params ); //
            this.$refs.formEditModal.show()

        },
        /**/
        sureSearch(val) {
            console.log('');
            console.log(val);
            // return false;
            let form_data= val.form_data;
            let form_des= val.form_des;
            let form_rules= val.form_rules;

            for(let i in form_data){
                this.params[i] = form_data[i];
            }

            /**/
            this.initParams();

            /**/
            this.getList()
                .then(list=>{
                    this.list=list
                })


        },
        /**/
        checkChooseListOne(){
            let curr_list_one = this.curr_list_one || {};//


            let index = curr_list_one._index;

            if (index !== undefined) {

                return true;

            } else {
                this.$message({
                    message: '',
                    type: 'warning'
                });
                return  false;

            }
        },
        /*table*/
        tableChoose(val) {

            console.log('curr_list_one',val)

            this.curr_list_one=val||{};
            // console.log(this.curr_list_one)


        },
    }
}