/**
 * @description element 
 * */

import {Message, Loading, Progress} from 'element-ui';
import utils from "./utils";

export default {
    Message,
    Loading(ele, text = '...') {
        return Loading.service({
            lock: true,
            text: text,//
            spinner: 'el-icon-loading',//
            // background: 'rgba(0, 0, 0, 0.7)',//
            target: ele
        })
    },
    /**/
    telCheck(rule, value, callback) {

        if (!value) {
            callback(new Error(''));
        } else {
            let re = utils.checkParams(value, 'tel')
            if (!re.is_check) {
                callback(new Error(''));
            } else {
                callback();
            }
        }
    }
}