/*

*/
<template>
    <div class="role role-manage" v-wechat-title="$route.meta.title">
        <!--  -->
        <div style="height: 46px; line-height: 46px; overflow: hidden;">
            <el-row>
                <el-col :span="6">
                    <div style="padding-left:16px;">
                        <i class="el-icon-s-unfold"></i>
                        <span style="padding-left:9px;">
                            {{$store.state.AdminData.active_title}}
                        </span>
                    </div>
                </el-col>

                <el-col :span="18">
                    <div style="text-align: right; ">
                        <el-link @click="onSubMenu('onRefresh',true)" class="menu"></el-link>
                        
                        <el-link
                            class="menu" 
                            @click="onSubMenu(item)"
                            v-for="(item,index) in $store.state.AdminData.right_menus" 
                            :key="index">
                            {{item.name}}
                        </el-link>
                    </div>
                </el-col>
            </el-row>
        </div>

        <!--  -->
        <div style="border-top: solid 1px #f2f1f4;overflow: hidden;">
            <el-table 
                ref="role"
                :data="rows"
                row-key="uuid"
                :height="height - 60 - 48"
                v-loading="loading"
                :default-expand-all="true"
                element-loading-text=""
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"

                :highlight-current-row="true"
                @current-change="onSelectRow"
                style="width: 100%" 
                size="mini">
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="name" label=""></el-table-column>
            </el-table>
        </div>

        <!--  -->
        <el-dialog 
            title=""
            width="450px"
            :visible.sync="add_show">
            <el-form :model="AddFormData" label-width="80px" size="small">
                <el-form-item label=":">
                    <el-cascader 
                        clearable
                        :options="add_rows"
                        :props="{expandTrigger: 'hover',value:'uuid', label:'name',emitPath:false}"
                        placeholder=""
                        v-model="AddFormData.parent_uuid">
                    </el-cascader>
                </el-form-item>

                <el-form-item label=":">
                    <el-input v-model="AddFormData.name" />
                </el-form-item>


            </el-form>

            <span slot="footer">
                <el-button @click="add_show = false" size="small"></el-button>
                <el-button type="primary" @click="onAddSubmit" size="small"></el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog 
            title=""
            width="450px"
            :visible.sync="edit_show">
            <el-form :model="EditFormData" label-width="80px" size="small">
                <el-form-item label=":">
                    <el-cascader 
                        clearable 
                        :options="edit_rows"
                        :props="{checkStrictly:true,expandTrigger: 'hover',value:'uuid', label:'name',emitPath:false}"
                        placeholder=""
                        v-model="EditFormData.parent_uuid">
                    </el-cascader>
                </el-form-item>

                <el-form-item label=":">
                    <el-input v-model="EditFormData.name" />
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="edit_show = false" size="small"></el-button>
                <el-button type="primary" @click="onEditSubmit" size="small"></el-button>
            </span>
        </el-dialog>


        <el-drawer
            size="800px"
            :append-to-body="true"
            :visible.sync="set_show">
            <div slot="title"></div>
            <div :style="{padding:'0 0 0 20px', height:height - 120 + 'px',overflow:'auto'}">
                <el-form :model="SetFormData" size="small">
                    <fieldset class="margin" v-for="item in SetMenuList" :key="item.uuid">
                        <legend class="padding">
                            <el-checkbox 
                                @change="onCheckSelect(item, null, null, 'g')"
                                :value="item.checked" 
                                :label="item.name"></el-checkbox>
                        </legend>

                        <div v-for="citem in item.children" :key="citem.uuid">
                            <span v-if="typeof citem.children == 'undefined'" class="span-check">
                                <el-checkbox 
                                    @change="onCheckSelect(item,citem, null, 'm')"
                                    :value="citem.checked" 
                                    :label="citem.name"></el-checkbox>
                            </span>

                            <fieldset v-else class="margin">
                                <legend class="padding">
                                    <el-checkbox 
                                        @change="onCheckSelect(item,citem, null, 'm')"
                                        :value="citem.checked" 
                                        :label="citem.name"></el-checkbox>
                                </legend>

                                <div class="padding">
                                    <span class="span-check" :key="sitem.uuid" v-for="sitem in citem.children">
                                        <el-checkbox 
                                            @change="onCheckSelect(item, citem, sitem, 's')"
                                            :value="sitem.checked" 
                                            :label="sitem.name"></el-checkbox>
                                    </span>
                                </div>
                            </fieldset>
                        </div>
                    </fieldset>
                </el-form>
                <el-button @click="onSetMenuSubmit" type="primary" size="small" style="position: fixed;bottom: 10px;right: 340px;"> </el-button>
            </div>
        </el-drawer>
    </div>
</template>

<script>
    import Vue from "vue";
    import store from "@/store";
    import { cookie_get } from "../../_commom/connectorHttp/manageHttp.js";
    import { ManageRoleList } from "../../_commom/connectorHttp/manageHttp.js";
    import { ManageRoleAdd } from "../../_commom/connectorHttp/manageHttp.js";
    import { ManageRoleEdit } from "../../_commom/connectorHttp/manageHttp.js";
    import { ManageRoleDel } from "../../_commom/connectorHttp/manageHttp.js";
    import { ManageRoleSetMenu } from "../../_commom/connectorHttp/manageHttp.js";
    import lime from "@/lime.js";
    import util from "@/util.js";

    if (!store.state.RoleData) {
        Vue.set(store.state, 'RoleData', {
            
        });
    }

    export default {
        data() {
            return {
                list:[],
                rows:[],
                loading:false,

                curr_row:null,

                // 
                add_show:false,
                add_rows:[],
                AddFormData:{
                    name:'',
                    parent_uuid:'', // 
                },


                edit_show:false,
                edit_rows:[],
                EditFormData:{
                    name:'',
                    parent_uuid:''
                },

                set_show:false,
                SetMenuList:[],
                SetFormData:{
                    uuid:'',
                    menu_uuids:[]
                }
            }
            //return store.state.RoleData;
        },
        computed:{
            width:() => {
                return store.state.AppData.width;
            },
            height:() => {
                return store.state.AppData.height;
            },
        },
        created(){
            this.init();
        },
        methods:{
            //  menu: local
            onSubMenu(menu, local = false) {
                util.submenu(menu,this,cookie_get(), local);
            },
            
            // 
            init() {
                this.loading = true;
                ManageRoleList({
                    login_token:cookie_get(),
                }).then(res => {
                    this.loading = false;
                    this.curr_row = null;
                    this.$refs.role.setCurrentRow();
                    this.list = res.data;
                    this.rows = util.toTree(res.data);
                });
                // 
                setTimeout(() => {
                    this.loading = false;
                }, 10000);
            },

            // 
            onRefresh() {
                this.init();
            },
            
            // 
            onSelectRow(row){
                this.curr_row = row;
            },

            // 
            handleAdd() {
                this.AddFormData = {
                    name:'',
                    parent_uuid:'',
                }
                // ,
                if (!util.empty(this.curr_row)) {
                    this.AddFormData.parent_uuid = this.curr_row.uuid;
                }

                let _rows = [];
                this.add_rows = [];
                this.list.forEach(item => {
                    _rows.push({
                        name:item.name,
                        parent_uuid:item.parent_uuid,
                        uuid:item.uuid
                    })
                });
                this.add_rows = util.toTree(_rows);
                this.add_show = true;
            },
            // 
            onAddSubmit() {
                this.AddFormData.login_token = cookie_get();
                ManageRoleAdd(this.AddFormData).then(res => {
                    this.init();
                    this.add_show = false;
                }).catch(err => {
                    // this.$message.error(err.msg);
                })
            },

            // 
            handleEdit() {
                if (util.empty(this.curr_row)) {
                    this.$message.error('');
                    return;
                }

                let _rows = [];
                this.edit_rows = [];
                this.list.forEach(item => {
                    if (item.uuid != this.curr_row.uuid) {
                        _rows.push({
                            name:item.name,
                            parent_uuid:item.parent_uuid,
                            uuid:item.uuid,
                        });
                    }
                });

                this.edit_rows = util.toTree(_rows);
                this.EditFormData.parent_uuid = this.curr_row.parent_uuid;
                this.EditFormData.name = this.curr_row.name;
                this.edit_show = true;
            },
            // 
            onEditSubmit() {
                this.EditFormData.login_token = cookie_get();
                this.EditFormData.uuid = this.curr_row.uuid;

                ManageRoleEdit(this.EditFormData).then(res => {
                    this.init();
                    this.edit_show = false;
                }).catch(err => {
                    this.$message.error(err.msg);
                });
            },


            // 
            handleDel(menu) {
                if (util.empty(this.curr_row)) {
                    this.$message.error('');
                    return;
                }

                this.$confirm('?', '').then(() => {
                    ManageRoleDel({
                        login_token:cookie_get(),
                        uuid:this.curr_row.uuid
                    }).then(res => {
                        this.init();
                        this.$message.success('');
                    }).catch(err => {
                        this.$message.error(err.msg);
                    })
                })
            },

            // 
            handleSetMenu(menu) {
                if (util.empty(this.curr_row)) {
                    this.$message.error('');
                    return;
                }
                lime.req({
                    module:'ManageRoleSetMenu',
                    ver:'1.0.0',
                    relation_module:'ManageMenuList',
                    relation_ver:'1.0.0'
                },{
                    login_token:cookie_get(),
                    role_uuid:this.curr_row.parent_uuid
                }).then(res => {
                    // ,
                    this.SetMenuList = [];
                    console.log(res.data)
                    res.data.forEach(item => {
                        console.log(44444444445)
                        item.checked = true;//////////////////////////////////////////////
                    })

                    // 
                    lime.req({
                        module:'ManageRoleSetMenu',
                        ver:'1.0.0',
                        relation_module:'ManageMenuList',
                        relation_ver:'1.0.0'
                    }, {
                        login_token:cookie_get(),
                        role_uuid:this.curr_row.uuid
                    }).then(cres => {
                        console.log(cres.data)
                        cres.data.forEach(sitem => {
                            console.log(this.SetFormData.menu_uuids)
                            sitem.checked = true;/////////////////////////////////////
                            this.SetFormData.menu_uuids.push(sitem.uuid);
                            
                        })

                        cres.data.forEach(item1 => {
                            console.log(333333335)
                            console.log(this.SetFormData.menu_uuids.includes(item1.uuid))
                            if (this.SetFormData.menu_uuids.includes(item1.uuid)) {
                                item1.checked = true;
                                console.log(111111)
                            } else {
                                item1.checked = false;
                                console.log(222222)
                            }
                        })
                    });

                    this.SetMenuList = util.toTree(res.data);
                    this.set_show = true;
                }).catch(err => {
                    this.$message.error(err.msg);
                })
            },

            // 
            onCheckSelect(item,citem,sitem, type) {
                if (type == 'g'){
                    console.log(item.checked);
                    item.checked = !item.checked;

                    item.children.forEach(_citem => {
                        _citem.checked = item.checked;
                        if (typeof _citem.children != 'undefined') {
                            _citem.children.forEach(_sitem => {
                                _sitem.checked = item.checked;
                            })
                        }
                    })
                }

                if (type == 'm'){
                    citem.checked = !citem.checked;
                    
                    if (typeof citem.children != 'undefined') {
                        citem.children.forEach(_sitem => {
                            _sitem.checked = citem.checked;
                        })
                    }
                    

                    // 
                    if (citem.checked == true) {
                        // ,
                        item.checked = true;
                    } else {
                        // ,,,,
                        let _other_citem_checked = false;
                        if (typeof item.children != 'undefined') {
                            item.children.forEach(_citem => {
                                if (_citem.checked == true){
                                    _other_citem_checked = true;
                                }
                            })
                        }

                        if (_other_citem_checked == true){
                            item.checked = true;
                        } else {
                            item.checked = false;
                        }
                    }
                }
                if (type == 's') {
                    sitem.checked = !sitem.checked;

                    if (sitem.checked == true){
                        item.checked = true;
                        citem.checked = true;
                    }
                }
                this._getMenu();
            },
            // 
            _getMenu() {
                this.SetFormData.menu_uuids = [];
                this.SetMenuList.forEach(_item => {
                    if (_item.checked == true) {
                        this.SetFormData.menu_uuids.push(_item.uuid);
                        if (typeof _item.children != 'undefined') {
                            _item.children.forEach(_citem => {
                                if (_citem.checked == true) {
                                    this.SetFormData.menu_uuids.push(_citem.uuid);
                                    if (typeof _citem.children != 'undefined') {
                                        _citem.children.forEach(_sitem => {
                                            if (_sitem.checked == true) {
                                                this.SetFormData.menu_uuids.push(_sitem.uuid);
                                            }
                                        })
                                    }
                                }
                            })
                        }
                    }
                })
            },
            onSetMenuSubmit() {
                lime.req('ManageRoleSetMenu', {
                    login_token:lime.cookie_get('login_token'),
                    uuid:this.curr_row.uuid,
                    menu_uuids:this.SetFormData.menu_uuids
                }).then(res => {
                    this.$message.success('');
                    this.init();
                    this.set_show = false;
                }).catch(err => {
                    this.$message.error(err.msg);
                })
            }
        }
    }
</script>

<style scoped>
    .menu{
        display: inline-block;
        padding:0 16px;
        text-align: center;
    }


    .margin{
        margin: 10px;
        border:solid 1px #ccc;
        border-radius: 5px;
    }

    .padding{
        padding:10px;
    }

    .my-menu{
        margin: 7px; 
        text-align: right; 
        height: 35px; 
        line-height: 35px;
    }

    .span-check {
        margin-left: 30px; 
        display: inline-block; 
        min-width: 120px;
    }
</style>
<style lang="less">
    .role-manage{
        .el-cascader{
            width: 330px;
        }
    }
    .el-table--scrollable-y .el-table__body-wrapper{
        overflow-y: hidden !important;
    }
</style>